.Detail {
  background-color: #f0f0f0;
  padding: 3rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
}
p {
  max-width: 20rem;
  margin: auto;
  text-align: left;
  font-family: Montserrat,sans-serif;
  font-size: 1.1rem;
  padding: 0.2rem;
}

button {
  height: 2rem;
  width: 4rem;
  border-radius: 0.5rem;
  background-color: black;
  color: white;
}

.detailLabel {
  min-width: 1.4rem;
  padding-right: 2rem;
}

td {
  text-align: left;
  margin: 1rem;
  height: 2rem;
}
