.Home {
  text-align: center;
  font-family: Montserrat,sans-serif;
}
form {
  margin-top: 3rem;
  text-align: center;
  background-color: #f0f0f0;
  width: 100%;
  padding: 3rem;
}

table {
  margin: auto;
  border-spacing: 0px;
}

th {
  font-size: 2.4rem;
  margin-bottom: 2rem;
  height: 5rem;
  line-height: 5rem;
}

.homeLabel {
  text-align: left;
  min-width: 5rem;
  height: 3rem;
  padding: 0.3rem;
}

input {
  min-width: 12rem;    
  height: 1.2rem;
  border-radius: 0.2rem;
}

button {
  border-radius: 0.5rem;
  background-color: black;
  color: white;
  margin-top: 2rem;
}

tbody {
  background-color: #f0f0f0;
}
